import './App.css';
import { Controller, Scene } from 'react-scrollmagic';
import ExampleDotMatrix from './components/ExampleDotMatrix';
import ShootingsMap from './components/ShootingsMap';
import ParentSize from '@visx/responsive/lib/components/ParentSize';
import VoronoiDiagram from './components/VoronoiDiagram';
import HomicideRateBarChart from './components/HomicideRateBarChart.jsx';
import useStore from './store/index';
import TextTrigger from './components/TextTrigger';
import ShootingsHomicidesIncarcerationChart from './components/ShootingsHomicidesIncarcerationChart';
import SocialFactorsMap from './components/SocialFactorsMap';
import covidPlaceholder from './assets/images/covid-placeholder.png';
import HeaderText from './components/HeaderText';
import { useRef } from 'react';



function App() {
  // for navigating the map timelines
  const ref1993 = useRef(null);
  const ref2019 = useRef(null);
  const ref2020 = useRef(null);

  const refDisconnectedYouth = useRef(null);
  const refCommmunityTrust = useRef(null);
  const refUnemployment = useRef(null);
  const refGraduationRate = useRef(null);
  const refPovertyRate = useRef(null);

  const getRefPosition = (duration, index, total) => `calc(-99vh + ${(duration * (index/total))}px)`; 

  const headerTexts = useStore((store) => store.headerTextArray);
  return (
    <div>
      <div className='grid grid-cols-12 fixed w-screen p-16'>
        <div className="col-span-3">
          <blockquote className="relative block w-full h-screen"><HeaderText texts={headerTexts} /></blockquote>
        </div>
      </div>
      <Controller>
        <Scene duration="3000" triggerHook="onLeave" pin>
          {
            (progress, event) => (
              <div className="w-full h-screen grid grid-cols-12 p-16">
                <TextTrigger triggerPoint={0.05} progress={progress} event={event} text="Safety depends on every New Yorker and every neighborhood." />
                <TextTrigger triggerPoint={0.15} progress={progress} event={event} text="The city's history has shown that something more than the force of the criminal justice system..." />
                <TextTrigger triggerPoint={0.85} progress={progress} event={event} text="The city's history has shown that something more than the force of the criminal justice system keeps New York safe." />
                <div className="col-span-7 col-start-5 my-16">
                  <ParentSize>
                    {
                      ({width, height}) => <VoronoiDiagram progress={progress} width={width} height={height} />
                    }
                  </ParentSize>
                </div>
              </div>
            )
          }
        </Scene>
        
        <Scene duration={1800} pin triggerHook="onLeave">
          {
            (progress, event) => (
              <div className='w-screen h-screen p-16'>
                <TextTrigger progress={progress} event={event} text="For 30 years, New Yorkers have committed fewer murders and shootings, while fewer people were jailed." />
                <div className='grid grid-cols-12'>
                  <div className='col-span-7 col-start-5 h-screen'>
                    <h3>NYC homicides, shootings and incarceration, 1993-2021</h3>
                    <div style={{height: '500px'}}>
                      <ParentSize>
                        {
                          ({width, height}) => <ShootingsHomicidesIncarcerationChart prescroll={0.2} progress={progress} width={width} height={height} />
                        }
                      </ParentSize>
                    </div>
                  </div>
                </div>
              </div>
            )
          }
        </Scene>
        
        <Scene duration={300} pin triggerHook="onLeave">
          {
            (progress, event) => {
              return (
                <div className="grid grid-cols-12 w-screen h-screen p-16">
                  <TextTrigger progress={progress} event={event} text="Today, New York City has the lowest murder rate of the top 5 largest cities in the US." />
                  <TextTrigger progress={progress} event={event} triggerPoint={0.99} text="" />
                  <div className="col-span-7 col-start-5">
                    <h3>Homicide rates of the five largest US cities, 2021</h3>
                    <ParentSize>
                      {
                        ({width, height}) => <HomicideRateBarChart progress={progress} width={width} height={width * .8} />
                      }
                    </ParentSize>
                  </div>
                </div>
              );
            }
          }
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          <div className="h-screen w-screen flex items-center content-center justify-center" style={{backgroundColor: '#6f7075'}}>
            <img src={covidPlaceholder} alt="covid placeholder" className='inline-block' />
          </div>
        </Scene>

        <Scene duration={400} triggerHook="onLeave" pin>
          <div className="h-screen w-screen flex items-center content-center justify-center bg-black">
            <div className='basis-1/3 text-white font-serif text-2xl text-center'>
              <p className='mb-4'>Some things changed, some things remained the same.</p>
              <p>Most importantly, NYC is still the safest city, but <span style={{color: 'rgb(221, 228, 76)'}}>not everyone is safe everywhere.</span></p>
            </div>
          </div>
        </Scene>
        
        <Scene duration={1800} triggerHook="onLeave" pin>
          {
            (progress, event) => (
              <div className='h-screen w-screen grid grid-cols-12 p-16'>
                <TextTrigger progress={progress} event={event} text="Lorem ipsum dolor, a description of this point goes here."></TextTrigger>
                <div className="col-span-8 col-start-5 h-screen">
                  <h3>NYC shooting victimization rates, ages 18-24 YTD 2021<br />(per 100,000 residents)</h3>
                  <ParentSize>
                    {
                      ({width, height}) => <div className='flex items-start mt-4 flex-row'><ExampleDotMatrix width={width} height={height} progress={progress} /></div>
                    }
                  </ParentSize>
                </div>
              </div>
            )
          }
        </Scene>
        
        <Scene duration={1200} pin triggerHook="onLeave" pinSettings={{pushFollowers: false}}>
          {
            (progress, event) => (
              <div className="h-screen w-screen grid grid-cols-12 p-16">
                <TextTrigger progress={progress} event={event} text="The city's poorest neighborhoods, home almost entirely to Black and Latino New Yorkers bear the brunt of violence. 2/3 of the city's shootings occur in Brooklyn and the Bronx, and that's been consistent over time." />
                <div className="col-span-8 col-start-5">
                  <h3>Number of shootings by precinct, 1993, 2019, and 2020</h3>
                  <div className="mt-4" style={{height: '500px'}}>
                    <ShootingsMap yearRefs={{'1993': ref1993, '2019': ref2019, '2020': ref2020}} progress={progress} />
                  </div>
                </div>
              </div>
            )
          }
        </Scene>

        <div style={{height: '1200px', position: 'relative', zIndex: -1}}>
          <a ref={ref1993} style={{position: 'absolute', top: getRefPosition(1200, 0, 3)}} id="map1993" name="map1993"></a>
          <a ref={ref2019} style={{position: 'absolute', top: getRefPosition(1200, 1, 3)}} id="map2019" name="map2019"></a>
          <a ref={ref2020} style={{position: 'absolute', top: getRefPosition(1200, 2, 3)}} id="map2020" name="map2021"></a>
        </div>

        <Scene duration={1200} pin pinSettings={{pushFollowers: false}} triggerHook="onLeave">
          {
            (progress, event) => (
              <div className="h-screen w-screen grid grid-cols-12 p-16">
                <TextTrigger progress={progress} event={event} text="And the conditions that incubate violence..." />
                <div className="col-span-8 col-start-5">
                  <SocialFactorsMap 
                    factorRefs={{'Disconnected Youth': refDisconnectedYouth, 'Community Trust': refCommmunityTrust, 'Unemployment': refUnemployment, 'Graduation Rate': refGraduationRate, 'Poverty Rate': refPovertyRate}} 
                    progress={progress}
                  />
                </div>
              </div>
            )
          }
        </Scene>

        <div style={{height: '1200px', position: 'relative', zIndex: -1}}>
          <a ref={refDisconnectedYouth} style={{position: 'absolute', top: getRefPosition(1200, 0, 5)}} id="mapDisconnectedYouth"></a>
          <a ref={refCommmunityTrust} style={{position: 'absolute', top: getRefPosition(1200, 1, 5)}} id="mapCommmunityTrust"></a>
          <a ref={refUnemployment} style={{position: 'absolute', top: getRefPosition(1200, 2, 5)}} id="mapUnemployment"></a>
          <a ref={refGraduationRate} style={{position: 'absolute', top: getRefPosition(1200, 3, 5)}} id="mapGraduationRate"></a>
          <a ref={refPovertyRate} style={{position: 'absolute', top: getRefPosition(1200, 4, 5)}} id="mapPovertyRate"></a>
        </div>

        <Scene duration={1} triggerHook="onEnter" pin>
          {
            (progress, event) => (
              <div className="h-screen w-screen flex items-center content-center justify-center">
                <TextTrigger event={event} progress={progress} text="" />
                <div className='basis-1/3 font-serif text-center'>
                  <p className="text-4xl mb-8">It doesn't have to be this way.</p>
                  <a className="bg-black p-4 uppercase text-sm text-white font-mono" href="#">Read issue 1 on gun violence</a>
                </div>
              </div>)
          }
        </Scene>
        
        {/*<Scene duration={1}>
          <div className="h-screen w-screen">
            <h1 className="text-4xl text-center py-16">NYC Police Precincts</h1>
            <div className="flex items-center justify-center flex-row">
              <div className="basis-1/2">
                <ParentSize>
                  {
                    ({width, height}) => <MyMap width={width} height={500} />
                  }
                </ParentSize>
              </div>
              <div className="basis-1/2">
                <ParentSize>
                  {
                    ({width, height}) => <MyMap width={width} height={500} />
                  }
                </ParentSize>
              </div>
            </div>
          </div>
        </Scene>*/}
      </Controller>
      <div className='bg-gray-600 relative' style={{height: '200px'}}></div>
    </div>
  );
}

export default App;
